import { Component, OnDestroy, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { Observable, Subscription } from 'rxjs';
import { EnumEventsPopUp } from 'src/models/Enum/EnumPopUp.enum';
import { BankModel } from 'src/models/PaymentMethods/bank.model';
import { CardModel } from 'src/models/PaymentMethods/card.model';
import { CashModel, CashModelResponse } from 'src/models/PaymentMethods/cash.model';
import { PaymentMethodsProvidersModel, PaymentMethodsProvidersModelResponse } from 'src/models/PaymentMethods/paymentMethodsProviders.model';
import { ActionPaymentMethod } from 'src/models/PaymentsMethodsPopUp/paymentMethodPopUp.model';
import { ClientService } from 'src/services/Client/client.service';
import { ManageDataService } from 'src/services/manage-data.service';
import { PaymentService } from 'src/services/payments/payment.service';

@Component({
  selector: 'app-payment-methods',
  templateUrl: './payment-methods.component.html',
  styleUrls: ['./payment-methods.component.scss']
})
export class PaymentMethodsComponent implements OnInit, OnDestroy {

  deposit:any = false;
  withdraw:any = false;
  select:any = false;
  action:any =false;
  default_method:any = false;
  bank_select:any = false;
  card_select:any = false;
  isChecked:any = false;
  button_disable:any = true;
  validating:any = false;
  banks_out = [];
  banks_in = [];
  cash_in = [];
  cash_out = [];
  card_in = [];
  card_out = [];
  methods_data = [];
  type_account:any = 'Checking account';
  provider:any;
  name:any;
  msg_events: any;
  index:any;

  actionToDo:string;


  public subscription: Subscription = new Subscription();

  constructor(private toastr:ToastrService, private payment:PaymentService, private client:ClientService,
    private dialogRef:MatDialogRef<PaymentMethodsComponent>, private manage_data:ManageDataService<any>,
    private translate:TranslateService) { }
  
  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  async ngOnInit(){

    this.actionToDo = await this.GetData();
    if(this.actionToDo == 'Deposit'){
      this.Deposit();
    }else if(this.actionToDo == 'Withdraw'){
      this.Withdraw();
    }


    this.GetClient().then((client_data:any)=>{
      this.GetProviders(client_data.domicile).then(async (data:PaymentMethodsProvidersModelResponse[])=>{
        if(data.length == 0){
          var msg = await this.translate.get('Errores.providersNotFound').toPromise();
          var title = await this.translate.get('general.error').toPromise();
          this.ShowError(msg, title);
        }
        data.forEach(element => {
         if(element.payment_type == 'bank'){
          if(element.direction == 'out'){
            this.banks_out.push(element)
          }else if(element.direction == 'in'){
            this.banks_in.push(element)
          }
         }else if(element.payment_type == 'cash'){
            if(element.direction == 'out'){
              this.cash_out.push(element)
            }else if(element.direction == 'in'){
              this.cash_in.push(element)
            }
         }else if(element.payment_type == 'card'){
          
            if(element.direction == 'out'){
              this.card_out.push(element)
            }else if(element.direction == 'in'){
              this.card_in.push(element)
            }
          }
        });
       })
    })

    
  }

  

  AccountType(data:any){
    if(data == 'Checking'){
      this.type_account = 'Checking account';
    }else if(data == 'Savings'){
      this.type_account = 'Savings account';
    }
  }

  Input(value:any){
   //console.log(value)
  }

  Select(value:string){
    console.log(value)
    if(value.includes('1')){
      this.Deposit();
    }else if(value.includes('2')){
      this.Withdraw();
    }

  }

  SelectTypePaymentMethod(value:string){
    console.log(value)
    if(value.includes('1')){
      this.GetAction('Bank');
    }else if(value.includes('2')){
      this.GetAction('Credit');
    }else if(value.includes('3')){
      this.GetAction('Cash');
    }

  }

  SelectPaymentMethod(value:string){
    let data = this.methods_data.find(element => element.provider_name == value)
    if(data){
      this.SelectMethod(data.payment_type, data.provider_id, data.provider_name)
    }
  }


  BankProcess(acc_name:any, acc_number:any){
    this.validating = true;
    const data:BankModel = {
      name: this.name,
      provider_id: this.provider,
      name_on_account: acc_name,
      account_type: this.type_account,
      account_number: acc_number,
      default_method: this.isChecked
    }
    this.SaveBankMethod(data).then((response:any)=>{
      console.log("BANK response",response)
      if(response == null || response.success == true){
        this.toastr.success("Success");
        this.Close();
        // this.router.navigate(['/home/portafolio'])
      }
    })
  }

  CardProcess(acc_name:any, acc_number:any, cvv:any, exp_date:any){
    this.validating = true;
    const data:CardModel = {
      name: this.name,
      provider_id: this.provider,
      name_on_account: acc_name,
      cc_number: acc_number,
      cvv: cvv,
      expiration_date: exp_date,
      default_method: this.isChecked
      
    }
    
    this.SaveCardMethod(data).then((response:any)=>{
      console.log("CARD response",response)
      if(response.success == true || response == null){
        this.toastr.success("Success");
        this.Close();
        // this.router.navigate(['/home/portafolio'])
      }
    })
  }

  SelectMethod(method:any, provider_id:any, name:any){
    
    this.name = name;
    this.provider = provider_id;

    switch (method) {
      case 'bank':
        this.card_select = false;
        this.bank_select = true;
        // this.action=false;
        break;

      case 'cash':
        this.bank_select = false;
        const data:CashModel = {
          name:  name,
          provider_id: provider_id,
          default_method: this.default_method
        }
        //console.log(data);
        this.SaveCashMethod(data).then((response:CashModelResponse)=>{
          // console.log("Save Cash",response)
          if( response == null){
            this.toastr.success("Success");
            this.Close();
            // this.router.navigate(['/home/portafolio'])
          }
        })

      
        break;
    
      default:
        // this.action = false;
        this.bank_select = false;
        this.card_select = true;
        break;
    }

  }

  //**************************************************************************************************
  //Return
  //**************************************************************************************************

  Return(){
    this.bank_select = false;
    this.card_select = false;
  }

  //**************************************************************************************************
  //PopUp Error
  //**************************************************************************************************

  ShowError(msg:string, title:string = "", disableTimeOut: boolean = true){
    console.error(msg);
    this.toastr.error(msg, title, {
      closeButton: true,
      disableTimeOut: disableTimeOut,        
      positionClass: 'toast-center-center',        
    });
  }

  //**************************************************************************************************
  //Get Data
  //**************************************************************************************************
    GetData(): Promise<string>{
      return new Promise<string>((resolve, reject) => {
        this.subscription.add(this.manage_data.getSubject(EnumEventsPopUp.ActionPaymentMethods.toString()).subscribe((data:ActionPaymentMethod)=>{
          resolve(data.action)
        }))  
      })
      
    }
  //**************************************************************************************************
  //Services
  //**************************************************************************************************


  SaveCardMethod(data:CardModel): Promise<any>{
    return new Promise((resolve, reject) => {

      this.subscription.add(this.payment.SaveCardPaymentMethod(data).subscribe(data=>{
        resolve(data);
      },err=>{
        console.log(err)
        this.toastr.error(err.error.payload, "Error", {
          closeButton: true,
          disableTimeOut: true,        
          positionClass: 'toast-center-center',        
  
        })
        this.validating = false;

      }))
      
    })
  }

  SaveBankMethod(data:BankModel): Promise<any>{
    return new Promise<any>((resolve, reject) => {

      this.subscription.add(this.payment.SaveBankPaymentMethod(data).subscribe(data=>{
        console.log('SaveBankPaymentMethod',data)
        resolve(data);
      },err=>{
        console.log(err)
        this.toastr.error(err.error.payload, "Error", {
          closeButton: true,
          disableTimeOut: true,        
          positionClass: 'toast-center-center',        
  
        })
        this.validating = false;
      }))
      
    })
  }

  SaveCashMethod(data:CashModel): Promise<CashModelResponse>{
    return new Promise<CashModelResponse>((resolve, reject) => {

      this.subscription.add(this.payment.SaveCashPaymentMethod(data).subscribe(data=>{
        resolve(data);
      },err=>{
        console.log(err)
        this.toastr.error(err.error.payload, "Error", {
          closeButton: true,
          disableTimeOut: true,        
          positionClass: 'toast-center-center',        
  
        })
        this.validating = false;

      }))
      
    })
  }

  GetProviders(domicile:any): Promise<PaymentMethodsProvidersModelResponse[]>{
    const data:PaymentMethodsProvidersModel = {
      country_code : domicile
    }
    return new Promise<PaymentMethodsProvidersModelResponse[]>((resolve, reject) => {
      this.subscription.add(this.payment.GetAllPaymentsProvidersByCountry(data).subscribe(data=>{
        resolve(data);
      },err=>{
        console.log(err)
        this.toastr.error(err.error.payload, "Error", {
          closeButton: true,
          disableTimeOut: true,        
          positionClass: 'toast-center-center',        
  
        })
      }))   
    })
  }

  GetClient(){
    
    return new Promise((resolve, reject) => {
      this.subscription.add(this.client.GetClientConfig().subscribe(data=>{
        resolve(data);
      },err=>{
        console.log(err)
        this.toastr.error(err.error.payload, "Error", {
          closeButton: true,
          disableTimeOut: true,        
          positionClass: 'toast-center-center',        
  
        })
      }))   
    })
  }

  //************************************************************************************************************

  async GetAction(method:any){
    switch (method) {
      case 'Bank':
        if(this.deposit){

          this.methods_data = this.banks_in;

        }else if(this.withdraw){

          this.methods_data = this.banks_out;

        }

        if(this.methods_data.length == 0){
          let msg = await this.translate.get('Errores.bankProvidersNotFound').toPromise();
          let title = await this.translate.get('general.error').toPromise();
          this.ShowError(msg,title)
        }

        this.bank_select = false;
        this.card_select = false;
        this.action = true;
        
        break;

      case 'Cash':
        if(this.deposit){

          this.methods_data = this.cash_in;

        }else if(this.withdraw){
          
          this.methods_data = this.cash_out;

        }

        if(this.methods_data.length == 0){
          let msg = await this.translate.get('Errores.cashProvidersNotFound').toPromise();
          let title = await this.translate.get('general.error').toPromise();
          this.ShowError(msg,title)
        }

        this.bank_select = false;
        this.card_select = false;
        this.action = true;

        break;

      case 'Credit':
        if(this.deposit){

          this.methods_data = this.card_in;

        }else if(this.withdraw){

          this.methods_data = this.card_out;

        }

        if(this.methods_data.length == 0){
          let msg = await this.translate.get('Errores.cardProvidersNotFound').toPromise();
          let title = await this.translate.get('general.error').toPromise();
          this.ShowError(msg,title)
        }

        this.action = true;
        this.card_select = false;
        this.bank_select = false;
        
        
        break;
  
      default:
        break;
    }
  }

  Deposit(){
    this.deposit = true;
    this.withdraw = false;
    this.select = true;
    this.action = false;
    this.bank_select = false;
    this.card_select = false;
  }

  Withdraw(){
    this.deposit = false;
    this.withdraw = true;
    this.select = true;
    this.action = false;
    this.bank_select = false;
    this.card_select = false;

  }
  //*********************************************************************************************
  //Close PopUp
  //*********************************************************************************************

  Close(){
    this.dialogRef.close();
  }
}
